.navbar {
    display: flex;
    justify-content:flex-start;
    align-items:center;
    background-color:#413333;
    height:50px;
    width:100%;
}
.menu{
    display: flex;
    justify-content: center;
    align-items:center;
    font-size: 1.2rem;
    background-color:#B2AFAF;
    width: 76%;
    height:60%;
    border-radius:28px;
    font-family: 'Rochester', cursive;
    font-weight:600;
    position: relative;
    left: 150px;
    
   animation-name:navtrans;
   animation-duration:2s;


}
.menuitem{
    color: #000000;
    margin: 0 51px;
}
.menuitem:hover{
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;
    transform: scale(1.2);
    color: #4D4646;
    cursor: pointer;

}
.cowandmilk{
    font-size: 1.5rem;
    position: relative;
    bottom:4px;

    

margin-left: 18%;
margin-right:1%;
}
.milk{
    display:inline-block;
    font-size:1rem;
    transform:rotate(45deg);
}
.linkitem{
    text-decoration: none;
}

@keyframes navtrans{
    from {width:0%;}
    to {width:76%;}
   }

